<template>
  <div :class="boxClass" :style="boxStyle">
    <slot></slot>
  </div>
</template>

<script setup lang="ts">
import { computed } from 'vue'

interface Props {
  direction?: 'horizontal' | 'vertical'
  padded?: boolean
  spacing?: number | string
  align?: 'start' | 'center' | 'end' | 'stretch'
  justify?: 'start' | 'center' | 'end' | 'space-between' | 'space-around'
  wrap?: boolean
}

const props = withDefaults(defineProps<Props>(), {
  direction: 'vertical',
  padded: false,
  spacing: 8,
  align: 'start',
  justify: 'start',
  wrap: false
})

// è®¡ç®—ç›’å­ç±»å
const boxClass = computed(() => ({
  'libui-box': true,
  [`libui-box--${props.direction}`]: true,
  'libui-box--padded': props.padded,
  'libui-box--wrap': props.wrap
}))

// è®¡ç®—ç›’å­æ ·å¼
const boxStyle = computed(() => {
  const style: Record<string, any> = {
    display: 'flex'
  }
  
  // æ–¹å‘
  style.flexDirection = props.direction === 'vertical' ? 'column' : 'row'
  
  // é—´è·
  if (props.spacing) {
    const gap = typeof props.spacing === 'number' ? `${props.spacing}px` : props.spacing
    style.gap = gap
  }
  
  // å¯¹é½æ–¹å¼
  if (props.direction === 'vertical') {
    style.alignItems = props.align
    style.justifyContent = props.justify
  } else {
    style.alignItems = props.justify
    style.justifyContent = props.align
  }
  
  // æ¢è¡Œ
  if (props.wrap) {
    style.flexWrap = 'wrap'
  }
  
  // å†…è¾¹è·
  if (props.padded) {
    style.padding = '16px'
  }
  
  return style
})
</script>

<style scoped>
.libui-box {
  width: 100%;
  height: 100%;
}

.libui-box--horizontal {
  flex-direction: row;
}

.libui-box--vertical {
  flex-direction: column;
}

.libui-box--padded {
  padding: 16px;
}

.libui-box--wrap {
  flex-wrap: wrap;
}
</style>