<template>
  <a-form
    v-bind="formProps"
    :style="formStyle"
    @finish="handleFinish"
    @finish-failed="handleFinishFailed"
  >
    <slot></slot>
  </a-form>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import type { FormProps } from 'ant-design-vue'

interface Props {
  title?: string
  size?: { width: number; height: number } | string
  layout?: 'horizontal' | 'vertical' | 'inline'
  centered?: boolean
  margined?: boolean
  model?: Record<string, any>
}

const props = withDefaults(defineProps<Props>(), {
  title: 'Window',
  size: () => ({ width: 400, height: 300 }),
  layout: 'vertical',
  centered: false,
  margined: false,
  model: () => ({})
})

// è®¡ç®—è¡¨å•æ ·å¼
const formStyle = computed(() => {
  const style: Record<string, any> = {}
  
  // å¤„ç†å°ºå¯¸
  if (typeof props.size === 'string') {
    const [width, height] = props.size.split(',').map(v => parseInt(v.trim()))
    style.width = `${width}px`
    style.height = `${height}px`
  } else if (props.size) {
    style.width = `${props.size.width}px`
    style.height = `${props.size.height}px`
  }
  
  // å±…ä¸­æ˜¾ç¤º
  if (props.centered) {
    style.margin = '0 auto'
  }
  
  // è¾¹è·
  if (props.margined) {
    style.padding = '20px'
  }
  
  return style
})

// è®¡ç®—è¡¨å•å±æ€§
const formProps = computed<FormProps>(() => ({
  layout: props.layout,
  model: props.model,
  labelCol: { span: 6 },
  wrapperCol: { span: 18 }
}))

// è¡¨å•æäº¤äº‹ä»¶
const handleFinish = (values: any) => {
  console.log('è¡¨å•æäº¤æˆåŠŸ:', values)
  emit('submit', values)
}

// è¡¨å•æäº¤å¤±è´¥äº‹ä»¶
const handleFinishFailed = (errors: any) => {
  console.log('è¡¨å•æäº¤å¤±è´¥:', errors)
  emit('submitFailed', errors)
}

const emit = defineEmits<{
  submit: [values: any]
  submitFailed: [errors: any]
}>()
</script>

<style scoped>
.libui-form {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}
</style>