<template>
  <a-input
    v-bind="inputProps"
    v-model:value="localValue"
    @change="handleChange"
    @focus="handleFocus"
    @blur="handleBlur"
  />
</template>

<script setup lang="ts">
import { computed, watch } from 'vue'
import type { InputProps } from 'ant-design-vue'

interface Props {
  modelValue?: string
  type?: string
  placeholder?: string
  value?: string
  readonly?: boolean
  disabled?: boolean
  bind?: string
}

const props = withDefaults(defineProps<Props>(), {
  modelValue: '',
  type: 'text',
  placeholder: '',
  value: '',
  readonly: false,
  disabled: false,
  bind: ''
})

const emit = defineEmits<{
  'update:modelValue': [value: string]
  change: [value: string, event: Event]
  focus: [event: FocusEvent]
  blur: [event: FocusEvent]
}>()

// æœ¬åœ°å€¼
const localValue = computed({
  get: () => props.modelValue || props.value || '',
  set: (value) => {
    emit('update:modelValue', value)
  }
})

// è®¡ç®—è¾“å…¥å±æ€§
const inputProps = computed<InputProps>(() => ({
  type: props.type,
  placeholder: props.placeholder,
  readonly: props.readonly,
  disabled: props.disabled,
  allowClear: true
}))

// ç›‘å¬ç»‘å®šé”®å˜åŒ–
watch(() => props.bind, (newBindKey) => {
  if (newBindKey) {
    console.log(`è¾“å…¥æ¡†ç»‘å®šåˆ°çŠ¶æ€é”®: ${newBindKey}`)
    // è¿™é‡Œå¯ä»¥æ·»åŠ çŠ¶æ€ç®¡ç†é€»è¾‘
  }
}, { immediate: true })

// äº‹ä»¶å¤„ç†
const handleChange = (event: Event) => {
  const target = event.target as HTMLInputElement
  const value = target.value
  emit('change', value, event)
  
  // å¦‚æœæœ‰ç»‘å®šé”®ï¼Œæ›´æ–°çŠ¶æ€
  if (props.bind) {
    console.log(`æ›´æ–°çŠ¶æ€ ${props.bind}: ${value}`)
    // è¿™é‡Œå¯ä»¥è°ƒç”¨çŠ¶æ€ç®¡ç†å™¨
  }
}

const handleFocus = (event: FocusEvent) => {
  emit('focus', event)
}

const handleBlur = (event: FocusEvent) => {
  emit('blur', event)
}
</script>

<style scoped>
.libui-input {
  width: 100%;
}
</style>