<script setup lang="ts">
import { defineProps, defineOptions, computed, inject } from 'vue'
import { 
  Input as AInput,
  Button as AButton,
  Checkbox as ACheckbox,
  Radio as ARadio,
  Select as ASelect,
  SelectOption as ASelectOption,
  Textarea as ATextarea,
  Progress as AProgress,
  Divider as ADivider,
  Table as ATable
} from 'ant-design-vue'
import type { ComponentConfig } from '@/types'

// è®¾ç½®ç»„ä»¶åç§°ä»¥ä¾¿é€’å½’å¼•ç”¨
defineOptions({
  name: 'ComponentRenderer'
})

const props = defineProps<{
  component: ComponentConfig
}>()

// æ³¨å…¥é€‰æ‹©å‡½æ•°
const selectComponent = inject<(component: ComponentConfig) => void>('selectComponent')

console.log('ComponentRendereræ³¨å…¥æ£€æŸ¥:', {
  componentType: props.component.type,
  componentId: props.component.id,
  hasSelectComponent: !!selectComponent,
  injectKey: 'selectComponent'
})

// ç‚¹å‡»å¤„ç†å‡½æ•°
const handleClick = (event: MouseEvent) => {
  console.log('ç»„ä»¶ç‚¹å‡»:', props.component.type, props.component.id, { 
    selectComponent: !!selectComponent,
    eventTarget: event.target,
    eventCurrentTarget: event.currentTarget
  })
  event.stopPropagation()
  if (selectComponent) {
    console.log('è°ƒç”¨selectComponent:', props.component)
    selectComponent(props.component)
  } else {
    console.warn('selectComponentæ³¨å…¥å¤±è´¥ï¼Œæ— æ³•é€‰æ‹©ç»„ä»¶')
  }
}

// è®¡ç®—çª—å£å°ºå¯¸æ ·å¼
const windowStyle = computed(() => {
  if (props.component.type !== 'window') return {}
  
  const style: Record<string, any> = {}
  const { size, centered } = props.component.properties
  
  console.log('çª—å£å°ºå¯¸è§£æ:', { size, centered, type: typeof size })
  
  // è§£æå°ºå¯¸
  if (size) {
    if (typeof size === 'string') {
      const [width, height] = size.split(',').map(v => parseInt(v.trim()))
      console.log('è§£æå°ºå¯¸å­—ç¬¦ä¸²:', { width, height, isNaNWidth: isNaN(width), isNaNHeight: isNaN(height) })
      if (!isNaN(width)) style.width = `${width}px`
      if (!isNaN(height)) style.height = `${height}px`
    } else if (typeof size === 'object' && size.width && size.height) {
      style.width = `${size.width}px`
      style.height = `${size.height}px`
    }
  }
  
  // å±…ä¸­å¤„ç†
  if (centered === true || centered === 'true') {
    console.log('åº”ç”¨å±…ä¸­æ ·å¼')
    style.margin = '0 auto'
  }
  
  console.log('æœ€ç»ˆçª—å£æ ·å¼:', style)
  return style
})

// è®¡ç®—çª—å£å†…å®¹æ ·å¼ï¼ˆæ ¹æ®marginedå±æ€§ï¼‰
const windowContentStyle = computed(() => {
  if (props.component.type !== 'window') return {}
  
  const { margined } = props.component.properties
  const padding = margined === true || margined === 'true' ? '16px' : '8px'
  
  return { padding }
})
</script>

