<template>
  <div :class="gridClass" :style="gridStyle">
    <slot></slot>
  </div>
</template>

<script setup lang="ts">
import { computed } from 'vue'

interface Props {
  padded?: boolean
  gap?: number
  columns?: number
  rows?: number
}

const props = withDefaults(defineProps<Props>(), {
  padded: false,
  gap: 8,
  columns: 12,
  rows: undefined
})

// è®¡ç®—ç½‘æ ¼ç±»å
const gridClass = computed(() => ({
  'libui-grid': true,
  'libui-grid--padded': props.padded
}))

// è®¡ç®—ç½‘æ ¼æ ·å¼
const gridStyle = computed(() => {
  const style: Record<string, any> = {
    display: 'grid',
    gap: `${props.gap}px`
  }
  
  // è®¾ç½®ç½‘æ ¼åˆ—
  if (props.columns) {
    style.gridTemplateColumns = `repeat(${props.columns}, 1fr)`
  }
  
  // è®¾ç½®ç½‘æ ¼è¡Œï¼ˆå¦‚æœæœ‰ï¼‰
  if (props.rows) {
    style.gridTemplateRows = `repeat(${props.rows}, auto)`
  }
  
  return style
})
</script>

<style scoped>
.libui-grid {
  width: 100%;
  height: 100%;
}

.libui-grid--padded {
  padding: 16px;
}
</style>